/* ==========================================================================
  Colors
  Only define colors here. Don't use sass color functions, stick to hex values,
  or the color component in styleguide won't regenerate automatically anymore.
 ========================================================================== */

/* Grays
 ========================================================================== */

$color-gray-30: #F6F6F6;
$color-gray-50: #F2F2F2;
$color-gray-100: #E5E5E5;
$color-gray-150: #D9D9D9;
$color-gray-200: #CCCCCC;
$color-gray-300: #B3B3B3;
$color-gray-400: #999999;
$color-gray-500: #808080;
$color-gray-600: #666666;
$color-gray-700: #4D4D4D;
$color-gray-800: #333333;
$color-gray-900: #1A1A1A;

/* Primary colors
 ========================================================================== */

$color-red-800: #B32D3B;
$color-red-500: #FF4054;
$color-red-200: #FFB0B9;


/* Secondary colors
 ========================================================================== */

$color-green-500: #00A57D;
$color-blue-500: #1267FB;
$color-blue-200: #8AB5FF;
$color-yellow-500: #F0F096;
$color-orange-500: #FF8700;
$color-brown-500: #A29490;
$color-mauve-500: #744DFF;
$color-mint-500: #8AFFC2;
$color-beige-500: #FFFAE7;

/* Semantic colors
 ========================================================================== */

$color-primary: #FF4054;
$color-header: #000000;
$color-text: #4D4D4D;
$color-muted: #666666;
$color-border: #E5E5E5;
$color-disabled: #999999;
$color-background: #F2F2F2;
$color-error: #FF4054;
$color-success: #00A57D;
$color-warning: #F4C95D;
$color-link: #1267FB;

