/* ==========================================================================
  c-bordered-list-horizontal
   ========================================================================== */

.c-bordered-list-horizontal {

  display: flex;

  li {
    border-right: 0.1rem solid $color-gray-200;
    &:last-child {
      border: none;
    }
    a, span {
      display: block;
      padding: .4rem 0.8rem;
    }
  }
}

.c-bordered-list-horizontal--muted {
  a {
    color: $color-muted;
  }
}