/* ==========================================================================
  Component: navbar
  ---
  A container for navigational elements
   ========================================================================== */

.c-navbar {
  padding: 0 2.4rem;
  height: $g-bar-size-regular;
  width: 100%;
}

.c-navbar--unpadded {
  padding: 0;
}

.c-navbar--auto {
  height: auto;
}

.c-navbar--white {
  background: #FFF;
}

.c-navbar--bg-alt {
  background: $color-gray-50;
}

.c-navbar--fixed {
  position: sticky;
  top: 0;
  left: 0;
  z-index: $g-c-global-navbar-z;
  border-bottom: 1px solid $color-gray-150;
}

/* Bordered equivalents
   ========================================================================== */

.c-navbar--bordered-top {
  border-top: 0.1rem solid $color-gray-150;
}

.c-navbar--bordered-bottom {
  border-bottom: 0.1rem solid $color-gray-150;
}

/* Sizes
   ========================================================================== */

.c-navbar--small {
  height: $g-bar-size-medium;
}

.c-navbar--large {
  height: $g-bar-size-large;
}
